import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BroadcastSetupComponent } from './components/broadcast-setup/broadcast-setup.component';
import { BroadcastHistoryComponent } from './components/broadcast-history/broadcast-history.component';
import { BroadcastSendmessageComponent } from './components/broadcast-sendmessage/broadcast-sendmessage.component';
import {BroadcastgroupSetupComponent} from './components/broadcastgroup-setup/broadcastgroup-setup.component'
import {BroadcastGroupAssignmentComponent} from './components/broadcast-group-assignment/broadcast-group-assignment.component'
import {BroadcastRemoveuserComponent} from './components/broadcast-removeuser/broadcast-removeuser.component'
import { BroadcastAnnouncementsComponent } from './components/broadcast-announcements/broadcast-announcements.component';
import { BroadcastRoleComponent } from './components/broadcast-sendmessage/broadcast-role/broadcast-role.component';
import { BroadcastGroupComponent } from './components/broadcast-sendmessage/broadcast-group/broadcast-group.component';

const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: BroadcastSetupComponent},
  { path: 'groupsetup',component:BroadcastgroupSetupComponent},
  { path: 'sendmessage', component: BroadcastSendmessageComponent,
  children: [
    { path: 'role-based', component: BroadcastRoleComponent, pathMatch: 'full'},
    { path: 'group-based', component: BroadcastGroupComponent }, 
    { path: '**', redirectTo: 'role-based', pathMatch: 'full'}
  ]},
  { path: 'assignusers', component: BroadcastGroupAssignmentComponent},
  { path: 'removeusers', component: BroadcastRemoveuserComponent},
  { path: 'announcements', component: BroadcastAnnouncementsComponent,
  children: [
    { path: 'group-based-announcement', loadChildren: () => import('src/app/modules/ams/broadcast/group-based-announcement/group-based-announcement.module').then(m => m.GroupBasedAnnouncementModule) },
    { path: 'group-based-announcement',  loadChildren: () => import('src/app/modules/ams/broadcast/interest-based-announcement/interest-based-announcement.module').then(m => m.InterestBasedAnnouncementModule)}, 
    { path: '**', redirectTo: 'broad-cast-group-based', pathMatch: 'full'}
  ]
},
  { path: 'history', component: BroadcastHistoryComponent},
  { path: '**', redirectTo: 'setup', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BroadcastRoutingModule { }
